import wButton from '@/components/watch-login/watch-button.vue' //button
export default {
	components:{
		wButton
	},
	data() {
		return {
			windowHeight:0,//屏幕高度
			phone:'',//手机号
			password:'',//密码
			isRotate: false, //是否加载旋转
			modalName:'',//忘记密码
		}
	},
	onLoad() {
		var that = this
		uni.getSystemInfo({
		    success: function (res) {
				that.windowHeight = res.windowHeight
		        console.log('屏幕高度为'+res.windowHeight);
		    }
		});
		
	},
	
	methods: {
		//注册
		forgetClick(){
			uni.navigateTo({
				url:'register'
			})
		},
		//登录
		startLogin(){
			var that = this
			// 防抖动
			if(that.isRotate) return
			if(!(/^1[3456789]\d{9}$/.test(that.phone))){
				uni.showToast({
					title:'手机号格式有误',
					icon:'none'
				})
				return
			}
			console.log(that.password.length)
			if(that.password.length < 6){
				uni.showToast({
					title:'密码最少为6位',
					icon:'none'
				})
				return
			}
			that.isRotate = true
			setTimeout(function() {that.isRotate = false}, 5000);
			let data = {
				phone:that.phone,
				user_pwd:that.password
			}
			login(data)
		},
		//忘记密码
		showModal(){
			uni.navigateTo({
				url:'forget'
			})
		},
	}
}
